.ColorBox {
    width: 20%;
    height: 25%;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    cursor: pointer;
    margin-bottom: -4px;
}

.SingleColorPalette .ColorBox{
  height: 50%;
}

.go-back{
  background-color: black;
}

.copy-button {
    width: 100px;
    height: 30px;
    position: absolute;
    display: inline-block;
    top: 50%;
    left: 50%;
    margin-left: -50px;
    margin-top: -15px;
    text-align: center;
    outline: none;
    background: rgba(255, 255, 255, 0.3);
    font-size: 1rem;
    line-height: 30px;
    color: white;
    text-transform: uppercase;
    border: none;
    opacity: 0;
  }

  .go-back .copy-button{
    opacity: 1;
  }
  
  .ColorBox:hover .copy-button {
    opacity: 1;
    transition: 0.5s;
  }
  
  .box-content {
    position: absolute;
    width: 200px;
    left: 0px;
    bottom: 0px;
    padding: 10px;
    color: black;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 12px;
  }

  .isLight{
    color: rgba(0, 0, 0, 0.5) !important;
  }

  .isDark{
    color: white;
  }
  
  .see-more {
    background: rgba(255, 255, 255, 0.3);
    position: absolute;
    border: none;
    right: 0px;
    bottom: 0px;
    color: white;
    width: 60px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    text-transform: uppercase;
  }

  .copy-overlay{
      opacity: 0;
      width: 100%;
      height: 100%;
      z-index: 0;
      transition: transform 0.6s ease-in-out;
      /* transform: scale(0.1); */
  }

  .copy-overlay.show{
      opacity: 1;
      transform: scale(50);
      z-index: 10;
      position: absolute;
  }

  .copy-mssg {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 4rem;
    transform: scale(0.1);
    opacity: 0;
    color: white;
  }

  .copy-mssg.show {
    opacity: 1;
    transform: scale(1);
    z-index: 25;
    transition: all 0.4s ease-in-out;
    transition-delay: 0.3s;
  }
  
  .copy-mssg h1 {
    font-weight: 400;
    text-shadow: 1px 2px black;
    background: rgba(255, 255, 255, 0.2);
    width: 100%;
    text-align: center;
    margin-bottom: 0;
    padding: 1rem;
    text-transform: uppercase;
  }
  .copy-mssg p {
    font-size: 2rem;
    font-weight: 100;
  }